.viewPost{
    display: flex;
    gap: 10px;
    @media only screen and (max-width:768px){
    flex-direction: column;
    }

}
.showPost{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;  
    height: auto;
    padding: 25px;
    width: 550px;
    margin: 0px auto 25px;
    background-color: rgba(0, 0, 0, 0.6);
    border-radius: 8px;
    button{
      margin-top: 5px;
      border: 1px solid white;
    }
    .titlearea{
    width: inherit;
    height: auto;
    margin-bottom: 10px;
    background: transparent;
    font-size: 1.5rem;
    color: white;
    border: 0;
    }
    .textarea{
    font-size: 1rem;
    color: white;
    width: 550px;
    height: 250px;
    margin-bottom: 10px;
    background: transparent;
    border: 0;
    }
    @media only screen and (max-width:768px){
      width: 270px;
      padding: 15px;
      .textarea{
        width: inherit;
      }
    }
}
textarea{
  &::-webkit-scrollbar {
    width: 1vw;
  }
  &::-webkit-scrollbar-track {
    background-color: #5555;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #7775;
  }
}
.viewComment {
  display: flex;
  justify-content: space-between;
  align-items: center;
    border: 1px solid black;
    color: wheat;
    padding: 10px;
    margin: 10px;
    font-size: 15px;
    border-radius: 5px;
    background: #182E49;
    border-bottom:2px solid black;
    margin-top:10px;
    padding-bottom:10px;
    p{
      margin-bottom: 5px;
    }
}
.comment-trash{
  font-size: 1rem;
  border: 0;
  background: transparent;  
  color: white; 
  transition: all 0.5s ease 0s; 
}
.comment-trash:hover{
  color:red;
}
.postComment {
    width: 550px;
    margin: 0px auto 25px;
    background-color: rgba(99, 99, 99, 0.4);
    width: 40rem;
    height: 25rem;
    padding: 1rem;
    overflow: auto;
    cursor: pointer;
    border-radius: 8px;
    border-bottom:2px solid black;
    &::-webkit-scrollbar {
        width: 1vw;
      }
      &::-webkit-scrollbar-track {
        background-color: #5555;
      }
      &::-webkit-scrollbar-thumb {
        background-color: #7775;
      }
      @media only screen and (max-width:768px){
        width: 270px;
        border-bottom: 0;
        padding: 12px;
      }
}
.edit-post-btn{
  gap: 20px;
  button{
    cursor: pointer;
    text-transform: uppercase;
    border: 0px;
    color: white;
    background-color: black;
    text-decoration: none;
    padding: 12px 16px 12px 16px;
    border-radius: 4px;
    font-size: 10px;
    letter-spacing: 3px;
    transition: all 0.5s ease 0s;
  }
}